import { AuthContext } from "Boot/auth/AuthProvider";
import BaseIndexPage from "components/HOC/BaseIndexPage";
import { PROPERTY } from "components/Routes/Constants";
import UserHeader from "components/section-components/UserHeader";
import React, { useContext } from "react";
import { connect } from "react-redux";
import { Link, Redirect, Route } from "react-router-dom";
import { Card, CardBody, CardTitle, Col, Container, Row } from "reactstrap";
import FormWrapper from "./Components/FormWrapper";

const Index = ({ turnOffSubmitting, turnOnSubmitting, ...props }) => {
  const { onHandleLogin, isLoggedIn } = useContext(AuthContext);

  if (isLoggedIn)
    return (
      <Route>
        <Redirect to={`${PROPERTY}/create#step1`} />
      </Route>
    );
  return (
    <div className="signin">
      <Container>
        <UserHeader />
        <Row className="d-flex justify-content-center my-4">
          <Col md="5">
            <Card className="border-0 card-shadow-info">
              <CardTitle>
                <h2 className="sub-title mt-3 pl-4"> Account Information</h2>
              </CardTitle>
              <CardBody>
                <FormWrapper
                  {...props}
                  onSubmit={(values) =>
                    onHandleLogin(values, turnOffSubmitting, turnOnSubmitting)
                  }
                />
                <hr />
                <Link
                  className="text-primary "
                  onClick={() => (window.location.href = "/forgot-password")}
                >
                  Forgot Password?
                </Link>
              </CardBody>
            </Card>
            <p className="mt-2">
              Don't Have an Account?{" "}
              <Link
                className="text-primary "
                onClick={() => (window.location.href = "/signup")}
              >
                Sign Up
              </Link>
            </p>
          </Col>
        </Row>
      </Container>
    </div>
  );
};
const mapStateToProps = (state, props) => ({
  noBanner: true,
  isHideFooter: true,
  noHeader: true,
  isHideNavigation: true,
});
export default connect(mapStateToProps, null)(BaseIndexPage(Index));
