import { ReactStrapFormGroupInput } from "components/global-components/Components/Form";
import { FormikHOC } from "components/global-components/Components/ReactStrapFormik";
import { Field } from "formik";
import React from "react";
import { connect } from "react-redux";
import { Form } from "reactstrap";
import { initialValues } from "./InitialValues.json";
import { ValidationSchema } from "./validationSchema";

const FormWrapper = ({ handleSubmit, values, ...rest }) => {
  const handleKeypress = (e) => {
    if (e.charCode === 13) {
      handleSubmit();
    }
  };
  return (
    <Form>
      <Field
        label={<b>Email</b>}
        type="text"
        name="email"
        bsSize="sm"
        onKeyPress={handleKeypress}
        component={ReactStrapFormGroupInput}
      />

      <Field
        label={<b>Password</b>}
        type="password"
        name="password"
        bsSize="sm"
        onKeyPress={handleKeypress}
        component={ReactStrapFormGroupInput}
      />

      <p>
        By clicking below and registering with DirectRent I agree to the
        DirectRent{" "}
        <a className="text-primary" href="/#" target="_blank">
          Terms and Conditions
        </a>{" "}
        and{" "}
        <a className="text-primary" href="/#" target="_blank">
          Privacy Policy
        </a>
      </p>
    </Form>
  );
};
const mapStateToProps = (state, props) => ({
  initialValues: initialValues,
  validationSchema: ValidationSchema,
  submitButtonLabel: "Sign In",
});
const mapDispatchToProps = (dispatch) => ({});
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FormikHOC(FormWrapper));
